import React from 'react';
import '../../App.css'

const Navigation = ( { ClickNavigation } ) => {
	return (
		<nav style={{ zIndex:'10', top:'0', paddingTop:'1rem', width:'100%', display:'flex', justifyContent:'space-between', fontSize:'2vh'}} 
		className='fixed bg-white pv2' >
			<span
			alt="home" 
			style={{ fontFamily: 'GilroyExtraBold', cursor: 'pointer'}}
			href='.'
			onClick={ ClickNavigation }
			className='ml4 dib no-underline black b hover-moon-gray'>ROMAN G.
			</span>
			<span 
			className='mr4' 
			style={{ fontFamily:'Libre Baskerville'}}>
				<span onClick={ ClickNavigation } style={{cursor: 'pointer'}} className='.dib no-underline black ph1 mh2 hover-bg-light-red' href='.'>Portfolio</span>
				<span style={{cursor: 'default'}}> / </span>
				<span onClick={ ClickNavigation } style={{cursor: 'pointer'}} className='.dib no-underline black ph1 mh2 hover-bg-light-yellow' href='.'>About</span>
				<span style={{cursor: 'default'}}> / </span>
				<span onClick={ ClickNavigation } style={{cursor: 'pointer'}} className='.dib no-underline black ph1 mh2 hover-bg-light-blue' href='.'>Contact</span>
			</span>
		</nav>
	);
}

export default Navigation;