import Gallery from 'react-grid-gallery';

class Commercial extends Component {
	constructor(props){
        super(props);
        this.state = {
            images: this.props.images
        };
    }

	render() {
		return (
			<div className='pa5'
			style={{
            display: "block",
            minHeight: "1px",
            width: "100%",
            overflow: "auto"}}>
	            <Gallery
	            images={this.state.images}
	            enableLightbox={true}
	            rowHeight={300}
	            margin={4}
	            enableImageSelection={false}/>
            </div>
		);
	}
}

