import React from 'react';
import '../../App.css';

const About = () => {
	return (
		<div id="About" className="">
			<div className="Container">
					<div className="columnGrid aboutInfo pv4" >
					<div>
						<p style={{ fontFamily: 'GilroyLight'}} className="f7 ttu">A bit more about me </p>
						<hr />
					</div>
					<div>
						<p style={{ fontFamily: 'GilroyExtraBold'}} className="aboutTitle b">
							I’M A DESIGNER <br />
							FROM VENEZUELA <br />
							WITH A FOCUS IN <br />
							DIGITAL EXPERIENCES <br />
						</p>	
						<p className="aboutContent" style={{ fontFamily: 'GilroyLight'}}> 
						I'm <span className="b">Guillermo Roman</span>, a <span className="b">designer</span> and
						<span className="b"> visual artist</span> focused on exploring and developing immersive interactions between
						 <span className="b"> humans, nature, and technology.</span> I had the opportunity of working
						 with clients from different countries around the world, helping individuals,
						 startups and companies with their requirements.
						</p>
						<p className="aboutContent" style={{ fontFamily: 'GilroyLight'}}> 
						I had the opportunity to start my professional career at Bululú Estudios, involved in digital retouching,
						social and artistic photography, digital retouching, storyboarding, album layout and customer service. During that time,
						I started developing a taste for pixel perfect work, color and the way ideas can be communicated in the digital world. Since then,
						I've been working as a full-time digital designer and illustrating as a freelancer.
						</p>
						<p className="aboutContent" style={{ fontFamily: 'GilroyLight'}}> 
						I’ve been lucky enough to have had the chance to work with amazing people who encouraged me to keep challenging myself and in 2018,
						I moved to Buenos Aires to keep studying and growing as a designer. It was the most challenging, inspiring and rewarding experience in my life so far. Buenos Aires 
						is an incredible city and being able to meet a different culture has made me a better designer and better human being.  If you want to know more about me, my projects 
						or simply grab a cup of coffee, just send me an email. I would love to hear from you.
						</p>
					</div>
				</div>
			</div>
		</div>
	);
}

export default About;