import React from 'react';
import '../../App.css';

const Portfolio = ( {ClickNavigation} ) => {
	return (
		<div style={{display: 'flex', justifyContent:'center', alignItems:'center'}}className='fullscreen'>
			<div className=''>
			<p onClick={ ClickNavigation } style={{ fontFamily: 'GilroyExtraBold', cursor: 'pointer'}} className='dib black b ttu hover-bg-light-red'>Portrait</p> <br />
			<p onClick={ ClickNavigation } style={{ fontFamily: 'GilroyExtraBold', cursor: 'pointer'}} className='pv1 dib black b ttu hover-bg-light-yellow'>Social</p> <br />
			<p onClick={ ClickNavigation } style={{ fontFamily: 'GilroyExtraBold', cursor: 'pointer'}} className='dib black b ttu hover-bg-light-blue'>Commercial</p> <br />
			</div>
		</div>
	);
}

export default Portfolio;