import React, { Component } from 'react';
import Navigation from './components/Navigation/Navigation';
import Content from './components/Content/Content';
import About from './components/About/About';
import Contact from './components/Contact/Contact';
import Portfolio from './components/Portfolio/Portfolio';
import Portrait from './components/Portrait/Portrait';
import Social from './components/Social/Social';
import Commercial from './components/Commercial/Commercial';
import './App.css';


class App extends Component {
	constructor() {
		super();
		this.state = {
			route: 'home',
		}
	}

	onClickNavigation = (event) => {
		if (event.target.innerText === 'ROMAN G.') {
			this.setState({ route: 'home' })
		} else if (event.target.innerText === 'Portfolio') {
			this.setState({ route: 'portfolio' })
		} else if (event.target.innerText === 'About') {
			this.setState({ route: 'about' })
		} else if (event.target.innerText === 'Contact') {
			this.setState({ route: 'contact' })
		} else if (event.target.innerText === 'PORTRAIT') {
			this.setState({ route: 'portrait' })
		} else if (event.target.innerText === 'COMMERCIAL') {
			this.setState({ route: 'commercial' })
		} else if (event.target.innerText === 'SOCIAL') {
			this.setState({ route: 'social' })
		}
	}

  render () {
  	const { route } = this.state;
  	let pagebody;

  	if(route === 'home') {
	    pagebody = <Content />;
	} if (route === 'portfolio') {
		pagebody = <Portfolio ClickNavigation={this.onClickNavigation} />;
	} if (route === 'about') {
		pagebody = <About />;
    } if (route === 'contact') {
    	pagebody = <Contact />;
    } if (route === 'portrait') {
    	pagebody = <Portrait />;	
    } if (route === 'commercial') {
    	pagebody = <Commercial />;	
    } if (route === 'social') {
    	pagebody = <Social />;	
    }

    return (
		<div className="App">
	        <Navigation ClickNavigation={this.onClickNavigation} />
	        {pagebody}
		</div>
    );
  }
}


export default App;
